html {
    background: conic-gradient(
            from 200deg at 50.23% 40.06%,
            #fceeef -43.41deg,
            #e5d8fa 115.87deg,
            #fceeef 316.59deg,
            #e5d8fa 475.87deg
        ),
        rgba(255, 255, 255, 0.8);
    height: 100%;
    width: 100%;
}

.read_container {
	background-color: #ffffff;
	border-radius: 30px;
	display: flex;
	flex-direction: column;
	margin: 30px auto;
	padding: 30px 40px;
	width: 800px;
	/* width: 80%; */
	box-sizing: border-box;
	box-shadow: 2px 2px 2px #e1e1e1;
}

.read_title {
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 600;
	color: #2F1154;
}
.read_info {
	position: relative;
	color: #2F1154;
	padding-bottom: 10px;
}

.read_name{
	color: #808080;
	position: absolute;
    bottom: 27px;
    right: 5px;
}

.read_day {
	color: #808080;
	position: absolute;
    bottom: 10px;
    right: 5px;
}
.read_content {
	padding-bottom: 20px;
	margin-bottom: 20px;
	padding-top: 20px;
	resize: none;
	font-size: 20px;
	height: 300px;
	color: #2F1154;
	border-bottom: solid 2px #4A3763;
	border-top: solid 2px #4A3763;
}

.button_box {
	border-style:none;
	display: flex;
	justify-content: flex-end;
}

.read_button {
	cursor: pointer;
	background-color: white;
	color: #A171FF;
	border: solid 2px #A171FF;
	border-radius: 10px;
	font-size: 16px;
	padding: 5px 15px;
	margin: 3px;

}

.delete_button {
	cursor: pointer;
	background-color: #A171FF;
	color: white;
	border-style:none;
	border-radius: 10px;
	font-size: 16px;
	padding: 5px 15px;
	margin: 3px;
}
