html {
	background: conic-gradient(
			from 200deg at 50.23% 40.06%,
			#fceeef -43.41deg,
			#e5d8fa 115.87deg,
			#fceeef 316.59deg,
			#e5d8fa 475.87deg
		),
		rgba(255, 255, 255, 0.8);
	height: 100%;
	width: 100%;
}
.MyPageProfile__profile {
	display: flex;
	margin: 50px auto;
	width: 700px;
	height: 250px;
	justify-content: space-between;
	align-items: center;
	border-radius: 15px;
	box-shadow: 0 1px 8px rgba(0, 0, 0, 0.3);
	color: #2f1154;
	background-color: #fff;
	.MyPageProfile__container__left {
		position: relative;
		img {
			border-radius: 50%;
			margin-left: 50px;
			width: 160px;
			height: 160px;
			&:hover {
				opacity: 0.25;
			}
			box-shadow: 0 1px 8px rgba(0, 0, 0, 0.3);
		}

		.Profile__icon {
			color: #524fa1;
			font-size: 23px;
			position: absolute;
			bottom: 70px;
			right: 67px;
			z-index: -1;
		}
	}

	.MyPageProfile__container__right {
		width: 400px;
		height: 150px;
		display: flex;
		flex-direction: column;
		margin-right: 50px;

		.items {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 50px;

			// 별명
			span {
				margin-top: 7px;
				font-size: 26px;
				cursor: default;
			}
		}

		button {
			cursor: pointer;

			width: 90px;
			height: 35px;
			border: 0;
			outline: none;
			border: 2px solid #a171ff;
			background-color: #fff;
			border-radius: 10px;
			color: #a171ff;
			font-weight: 600;
			font-size: 14px;
		}

		.description {
			span {
				font-size: 17px;
				cursor: default;
				border-bottom: 1px solid #bcbcbc;
				padding-bottom: 5px;
				&::after {
					content: '';
					display: block;
					width: 65px;
					margin: 8px 0px 15px 0px;
				}
			}

			p {
				margin-top: 20px;
				font-size: 15px;
				cursor: default;
			}

			textarea {
				margin-top: 7px;
				display: block;
				resize: none;
			}
		}
	}
}
